<template>
  <b-container class="loginContainer">
    <b-row>
      <b-col class="d-flex justify-content-center align-items-center">
        <b-card align="center" title="Login " style="width:100%; max-width:30rem;">
          <b-form @submit.prevent="onSubmit">
            <b-form-group
              validated
              id="email-input-group"
              label="Email address:"
              label-for="email"
              valid-feedback="Good"
              invalid-feedback="Enter valid email"
            >
              <b-form-input
                id="email"
                v-model="form.email"
                type="email"
                required
                placeholder="Enter your email:  "
              ></b-form-input>
            </b-form-group>

            <b-form-group
              valid-feedback="Good"
              invalid-feedback="Your password should consists minimum of 8 characters."
              id="password-input-group"
              label="Your password: "
              label-for="password"
            >
              <b-form-input
                type="password"
                id="password"
                :state="passwordState"
                v-model="form.password"
                required
                placeholder="Enter your password: "
              ></b-form-input>
            </b-form-group>
            <b-button class="mr-4" type="submit" variant="success">
              <b-spinner v-if="loading" variant="warning"></b-spinner>
              <span v-if="!loading">Login</span>
            </b-button>
            <b-button to="/resetPassword" variant="warning">Forgot my password</b-button>
          </b-form>
        </b-card>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import axios from "axios";
import store from "../store/index";
export default {
  data() {
    return {
      form: {
        email: "",
        password: ""
      },
      loading: false,
      redirect: this.$router.currentRoute.query.redirect
    };
  },
  computed: {
    passwordState() {
      return this.form.password.length >= 8;
    }
  },
  methods: {
    onSubmit() {
      this.loading = true;
      store.commit("login");
      // store.dispatch("login", this);
    }
  }
};
</script>

